.header {
  background-image: url("../img/header-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #002921;
  box-shadow: 0px 0px 40px 5px rgba(0, 0, 0, 0.25);
  border-bottom: 1px solid #FFD759;
  padding: 26px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 29px;
  }
  &__logo {
    width: 217px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 29px;
    li a {
      font-family: 'CenturyGothic';
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      &:hover {
        color: #E9D869;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 67px;
  height: 45px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 8px;
    background: #F6E588;
    border-radius: 1px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #fff;
    }
    span:nth-child(1) {
      top: 18px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 18px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1600px) {
  .header {
    &__inner {
      gap: 20px;
    }
    &__list {
      gap: 20px;
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__inner {
      gap: 15px;
    }
    &__logo {
      width: 180px;
    }
    &__list {
      gap: 15px;
      li a {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    padding: 20px 0;
    &__list {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 65%;
      height: 100%;
      flex-direction: column;
      align-items: start;
      gap: 50px;
      background: #031A16;
      padding: 130px 25px 50px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      li a {
        font-size: 32px;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__list {
      gap: 30px;
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 12px 0;
    &__logo {
      width: 78px;
    }
    &__list {
      width: 77%;
      padding: 80px 25px 30px;
    }
  }
  .burger {
    width: 26px;
    height: 17px;
    span {
      height: 3px;
    }
    &--active {
      span:nth-child(1) {
        top: 7px;
      }
      span:nth-child(3) {
        bottom: 7px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__list {
      width: 100%;
    }
  }
}