.main-section {
  position: relative;
  background-image: url("../img/main-img.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 535px;
    background: linear-gradient(180.00deg, rgba(3, 26, 22, 0),rgb(3, 26, 22) 100%);
    pointer-events: none;
  }
  &__inner {
    width: 100%;
    min-height: 880px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0;
  }
  &__btn {
    width: 405px;
    height: 130px;
    padding: 0 10px;
    &:after {
      background-image: url("../img/btn-bg__main.svg");
    }
    span {
      font-size: 40px;
    }
  }
}
.info {
  position: relative;
  z-index: 2;
  padding-bottom: 95px;
  margin-top: -116px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 80px;
  }
  &__item {
    &-title {
      font-family: 'CenturyGothic';
      font-size: 48px;
      line-height: 120%;
      font-weight: 700;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 30px;
      p {
        font-size: 27px;
        line-height: 120%;
        font-weight: 500;
      }
    }
  }
  table {
    width: 100%;
    background: rgba(0, 85, 70, 0.2);
    border-collapse: collapse;
    border: 4px solid #005546;
    margin: 40px 0 80px;
    thead {
      tr {
        th {
          font-family: 'CenturyGothic';
          font-size: 40px;
          line-height: 120%;
          font-weight: 700;
          border: 1px solid #005546;
          padding: 14px 10px;
        }
      }
    }
    tbody {
      tr {
        td {
          text-align: center;
          font-size: 27px;
          line-height: 120%;
          border: 1px solid #005546;
          padding: 14px 10px;
        }
        td:nth-child(1) {
          text-align: start !important;
        }
      }
    }
  }

}
.banner {
  position: relative;
  margin: 80px 0;
  &__img {
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__desc {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    max-width: 830px;
    padding: 50px 50px 50px 100px;
  }
  &__title {
    font-size: 35px;
    line-height: 120%;
    font-weight: 700;
    color: #005C4C;
  }
  &__subtitle {
    font-size: 60px;
    line-height: 120%;
    font-weight: 700;
    color: #005C4C;
    margin-top: 50px;
  }
  &__btn {
    width: 340px;
    height: 110px;
    padding: 0 5px;
    margin-top: 50px;
    &:after {
      background-image: url("../img/btn-bg__banner.svg");
    }
    span {
      font-size: 35px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .info {
    &__items {
      gap: 50px;
    }
    &__item {
      &-title {
        font-size: 30px;
      }
      &-text {
        margin-top: 25px;
        p {
          font-size: 20px;
        }
      }
    }
    table {
      thead {
        tr {
          th {
            font-size: 30px;
          }
        }
      }
      tbody {
        tr {
          td {
            font-size: 20px;
          }
        }
      }
    }
  }
  .banner {
    &__desc {
      max-width: 600px;
    }
    &__title {
      font-size: 30px;
    }
    &__subtitle {
      font-size: 38px;
      margin-top: 20px;
    }
    &__btn {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main-section {
    &:after {
      height: 335px;
    }
    &__inner {
      min-height: 680px;
    }
  }
  .info {
    margin-top: -50px;
  }
}

@media screen and (max-width: 1200px) {
  .banner {
    &__desc {
      max-width: 400px;
      padding: 50px 35px;
    }
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 28px;
    }
    &__btn {
      width: 154px;
      height: 50px;
      &:after {
        background-image: url("../img/btn-bg__banner-tablet.svg");
      }
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main-section {
    &:after {
      height: 235px;
    }
    &__inner {
      min-height: 500px;
    }
    &__btn {
      width: 205px;
      height: 66px;
      &:after {
        background-image: url("../img/btn-bg__main-tablet.svg");
      }
      span {
        font-size: 22px;
      }
    }
  }
  .info {
    padding-bottom: 40px;
    table {
      border-width: 2px;
      margin: 40px 0;
      thead {
        tr {
          th {
            font-size: 22px;
            padding: 10px 5px;
          }
        }
      }
      tbody {
        tr {
          td {
            font-size: 15px;
            padding: 10px 5px;
          }
        }
      }
    }
  }
  .banner {
    margin: 40px 0;
  }
}

@media screen and (max-width: 768px) {
  .banner {
    max-width: 500px;
    margin: 40px auto 40px;
    &__img {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__desc {
      top: unset;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      text-align: center;
      padding: 50px 35px;
    }
    &__title,
    &__subtitle {
      color: #fff;
    }
    &__btn {
      margin: 20px auto 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .main-section {
    &:after {
      height: 135px;
    }
    &__inner {
      min-height: 330px;
    }
    &__btn {
      width: 106px;
      height: 34px;
      padding: 0 5px;
      &:after {
        background-image: url("../img/btn-bg__main-mobile.svg");
      }
      span {
        font-size: 12px;
      }
    }
  }
  .info {
    padding-bottom: 20px;
    margin-top: -20px;
    &__items {
      gap: 20px;
    }
    &__item {
      &-title {
        font-size: 16px;
      }
      &-text {
        gap: 10px;
        margin-top: 10px;
        p {
          font-size: 12px;
        }
      }
    }
    table {
      margin: 20px 0;
      thead {
        tr {
          th {
            font-size: 12px;
          }
        }
      }
      tbody {
        tr {
          td {
            font-size: 10px;
          }
        }
      }
    }
  }
  .banner {
    margin: 20px auto;
    &__desc {
      padding: 35px;
    }
    &__title {
      font-size: 10px;
    }
    &__subtitle {
      font-size: 18px;
      margin-top: 12px;
    }
    &__btn {
      width: 93px;
      height: 30px;
      margin: 12px auto 0;
      &:after {
        background-image: url("../img/btn-bg__banner-mobile.svg");
      }
      span {
        font-size: 9px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .info {
    table {
      thead {
        tr {
          th {
            font-size: 10px;
          }
        }
      }
      tbody {
        tr {
          td {
            font-size: 8px;
          }
        }
      }
    }
  }
}