.btn-green {
  position: relative;
  background: transparent;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }
  span {
    position: relative;
    z-index: 1;
    font-family: 'CenturyGothic';
    line-height: 120%;
    font-weight: 700;
    color: #fff;
  }
}
.up {
  position: fixed;
  z-index: 9;
  right: 25px;
  bottom: 50px;
  width: 91px;
  height: 91px;
  background: transparent;
  border: 7px solid #E9D869;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  img {
    width: 47px;
    height: auto;
  }
}

@media screen and (max-width: 992px) {
  .up {
    right: 15px;
    width: 50px;
    height: 50px;
    border-width: 4px;
    img {
      width: 25px;
    }
  }
}

@media screen and (max-width: 576px) {
  .up {
    right: 10px;
    width: 29px;
    height: 29px;
    border-width: 2px;
    img {
      width: 14px;
    }
  }
}